body {
  font-family: "M PLUS Rounded 1c", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.mv-sub {
  position: relative;
}

.mv-sub__inner {
  margin: 1.25rem 1.25rem;
}
@media screen and (min-width: 768px) {
  .mv-sub__inner {
    margin: 1.25rem 3.75rem;
  }
}

.mv-sub__img {
  width: 100%;
  height: 11.75rem;
}
@media screen and (min-width: 768px) {
  .mv-sub__img {
    height: 23.125rem;
  }
}

.mv-sub__heading {
  color: #fff;
  position: absolute;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.mv-sub__title {
  letter-spacing: 0.1em;
}
@media screen and (min-width: 768px) {
  .mv-sub__title {
    font-size: 2rem;
    letter-spacing: 0.1em;
  }
}

.mv-sub__lead {
  display: inline-block;
  margin-top: 0.625rem;
  letter-spacing: 0.12em;
  font-size: 0.75rem;
}
@media screen and (min-width: 768px) {
  .mv-sub__lead {
    margin-top: 1.0625rem;
    letter-spacing: 0.1em;
    font-size: 0.875rem;
  }
}